/**************************************
    IX - DarK Mode & Light Mode Toggle
**************************************/
/* The switch - the box around the slider */
    .switch {
        position: relative;
        display: inline-block;
        width: 50px;
        height: 24px;
    }
    
    /* Hide default HTML checkbox */
    .switch input {
        opacity: 0;
        width: 0;
        height: 0;
    }
    
    /* The slider */
    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #2B283A;
        -webkit-transition: .4s;
        transition: .4s;
    }
    
    .slider:before {
        position: absolute;
        content: "";
        height: 20px;
        width: 20px;
        left: 3px;
        bottom: 2px;
        background-color: white;
        -webkit-transition: .4s;
        transition: .4s;
    }

    
    input:focus + .slider {
        /*checked*/
        box-shadow: 0 0 1px #2B283A;
    }

    input:checked + .slider {
        /*checked*/
        background-color: #F5F5F5;
    }

    input:checked + .slider:before {
        background-color: #2B283A;
        -webkit-transform: translateX(26px);
        -ms-transform: translateX(26px);
        transform: translateX(26px);
    }
    

    .slider-text-night{
        color:var(--black-night);
    }

    .slider-text-light{
        color:rgb(166, 166, 168);
    }

    /* Rounded sliders */
    .slider.round {
        border-radius: 34px;
    }
    
    .slider.round:before {
        border-radius: 50%;
    }

/***********************************/

